       -ms-flex-pack: center;
           justify-content: center;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   margin-left: 15px;
   margin-right: 15px;
   margin-top: 10px;
   position: relative;
   top: 7px;
}
.menuIconCont
{

   width: 35%;
   height: auto;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
